
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {feedback} from '../config'
export default {
  name: 'feedback',
  head: {
    title: {
      inner: '意见反馈'
    }
  },
  components: {
    Group,
    Cell,
    XInput,
    XTextarea,
    XButton
  },
  data () {
    return {
      loading: false,
      form: {
        userId: '',
        content: '',
        phone: ''
      }
    }
  },
  mounted () {
    if (this.$localStorage.get('logined') !== 'false') {
      this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    } else {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: '该功能仅对登录用户提供！',
        time: '1000'
      })
    }
  },
  methods: {
    handleSubmit () {
      if (this.form.content.length < 20) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '反馈内容必须大于20个字符！',
          time: '1000'
        })
      } else if (!this.form.phone) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '手机号码不能为空！',
          time: '1000'
        })
      } else if (!this.$refs.tel.valid) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '手机号码有误！',
          time: '1000'
        })
      } else {
        this.$http({
          method: 'jsonp',
          url: feedback,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: this.form,
          before: () => {
            this.loading = true
          }
        })
        .then(res => {
          this.loading = false
          this.form.content = this.form.phone = ''
          if (res.body.status) {
            this.$vux.toast.show({
              type: 'text',
              width: '12em',
              position: 'bottom',
              text: '已收到您的反馈！',
              time: '1000'
            })
            this.$router.replace('/personer')
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '12em',
              position: 'bottom',
              text: res.body.msg,
              time: '1000'
            })
          }
        })
      }
    }
  }
}



